    @media (max-width: 768px)
      margin-bottom: 40px
      font-weight: 800
      font-size: 18px
      writing-mode: unset
      border-right: none
      padding: 3px
      flex-direction: row
      &:last-child
        border-left: none
      &:not(:last-child)
        margin-right: 0
        margin-bottom: 10px
