  &__icon
    display: none
    @media ( max-width: 768px )
      display: block
      position: relative
      flex: 0 0 20px
      // top: 18px
      // right: 10px
      width: 32px
      height: 20px
      border: none
      background-color: transparent
      cursor: pointer
      outline: none
      z-index: 25
    span
      position: absolute
      top: calc( 50% - 2px )
      left: 0
      width: 100%
      height: 4px
      background-color: $text
      transition: all 0.3s ease 0s
      &:first-child
        top: 0
      &:last-child
        top: auto
        bottom: 0
    &._active
      span
        transform: scale(0)
        &:first-child
          top: calc( 50% - 3px )
          transform: rotate(-45deg)
        &:last-child
          top: calc( 50% - 3px )
          transform: rotate(45deg)
