.accordeon
  &__item
    overflow: hidden
    transition: all 0.25s ease-out
  &__name
    position: relative
    cursor: pointer
    &::before
      content: ''
      position: absolute
      top: 8px
      right: 0
      transition: all 0.5s ease-out
      background: url(../images/icons/sprite.svg#plus) no-repeat center center
      height: 16px
      width: 16px
      background-color: transparent
      cursor: pointer
      outline: none
  &__content
    margin: 0
    max-height: 0
    opacity: 0
    overflow: hidden
    transition: all 0.5s ease-out
  &__title
    display: flex
    align-items: center
    justify-content: space-between
    width: 100%
    // cursor: pointer
  &__head
    padding: 35px 0
    color: $dark
  &__close
    position: absolute
    bottom: 19px
    right: 0
    text-align: right
    margin-top: 133px
  &__open
    & .btn
      transform: rotate(180deg)


.active .accordeon
  &__content
    opacity: 1
    max-height: max-content
    transition: all 0.5s ease-out
  &__title
    display: none
