.menu__item {
  position: relative;
  display: flex;
  flex-direction: column;
  font-weight: 400;
  font-size: 46px;
  line-height: 1.2173913043;
  color: #4E5871;
  transition: all 0.3s ease-in-out;
  writing-mode: vertical-lr;
  padding: 30px 39px 0;
  border-right: 2px solid #4E5871;
  height: 100%;
}
.menu__item:last-child {
