}
@media (max-width: 768px) {
  .menu__item {
    margin-bottom: 40px;
    font-weight: 800;
    font-size: 18px;
    writing-mode: unset;
    border-right: none;
    padding: 3px;
    flex-direction: row;
  }
  .menu__item:last-child {
    border-left: none;
  }
  .menu__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 10px;
  }
}
.menu__icon {
  display: none;
}
@media (max-width: 768px) {
  .menu__icon {
    display: block;
    position: relative;
    flex: 0 0 20px;
    width: 32px;
    height: 20px;
    border: none;
    background-color: transparent;
    cursor: pointer;
    outline: none;
    z-index: 25;
  }
}
.menu__icon span {
  position: absolute;
  top: calc(50% - 2px);
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #4E5871;
  transition: all 0.3s ease 0s;
}
.menu__icon span:first-child {
  top: 0;
}
.menu__icon span:last-child {
  top: auto;
  bottom: 0;
}
.menu__icon._active span {
  transform: scale(0);
}
.menu__icon._active span:first-child {
  top: calc(50% - 3px);
  transform: rotate(-45deg);
}
.menu__icon._active span:last-child {
  top: calc(50% - 3px);
  transform: rotate(45deg);
}

