    left: 30px;
  }
  a.donate__see::before {
    left: 68%;
  }
}
@media (max-width: 1100px) {
  .header__link {
    font-size: 50px;
  }
  .header__slide-album {
    left: 20px;
    width: 250px;
    height: 250px;
    font-size: 28px;
    bottom: -135px;
  }
  .header__slide-year {
    font-size: 50px;
  }
  .player__sound {
    width: 190px;
  }
  a.donate__see::before {
    left: 70%;
  }
}
@media (max-width: 1050px) {
  a.donate__see::before {
    left: 71%;
  }
}
@media (max-width: 1000px) {
  .header__slide-album {
    left: 10px;
  }
  a.donate__see::before {
    left: 72%;
  }
}
@media (max-width: 992px) {
  .header__slide-album {
    z-index: 1;
    bottom: 10px;
    color: #B4BAC7;
  }
  .header__slide-title {
    align-self: flex-end;
    margin-left: 30%;
    margin-bottom: 5%;
  }
  .header__slide-year {
    color: #F6F5F0;
  }
  .menu {
    margin-left: 0;
  }
  .albums.active .albums__show,
  .fonograms.active .fonograms__show {
    flex-direction: column;
  }
  .albums__head {
    margin-bottom: 30px;
  }
  .fonograms__head {
    max-width: none;
    margin-bottom: 30px;
  }
  .albums__info {
    position: relative;
    padding-right: 0;
  }
  .albums__img {
    width: 100%;
    min-height: 260px;
    height: 320px;
    margin-bottom: 0;
  }
  .albums__name {
    position: absolute;
    left: 20px;
    bottom: 20px;
    color: #F6F5F0;
  }
  .albums__name span {
    color: #B4BAC7;
  }
}
@media (max-width: 768px) {
  body {
    font-size: 18px;
    line-height: 1.2;
  }
  .heading {
    font-size: 36px;
    line-height: 1.2;
  }
  .btn {
    width: 80px;
    height: 80px;
  }
  .donate__btn {
    padding: 24px 0;
    font-size: 32px;
  }
  .header__link {
    font-size: 24px;
  }
  .header__socials {
    display: block !important;
    z-index: 21;
  }
  .header__since {
    display: none;
    overflow: hidden;
    width: 1px;
  }
  .header__slider {
    max-height: none;
  }
  .header__slide {
    flex-direction: column;
    height: auto;
  }
  .header__slide-album {
    z-index: 1;
    bottom: 0px;
    position: relative;
    color: #B4BAC7;
  }
  .header__slide-title {
    display: flex;
    align-items: center;
    height: 450px;
    margin-bottom: 16px;
    margin-left: 0;
    align-self: auto;
  }
  .header__slide-year {
    color: #4E5871;
  }
  .socials {
    display: none;
  }
  .about.active .about__show {
    flex-direction: column;
  }
  .about__head {
    border-left: 3px solid #4E5871;
    padding-left: 45px;
  }
  .about__info {
    max-width: none;
  }
}